import {useState, useEffect, useRef} from "react";

import './comicsList.scss';
import MarvelService from "../../services/MarvelService";
import ErrorMessage from "../errorMessage/ErrorMessage";
import Spinner from "../spinner/Splinner";
import {Link} from "react-router-dom";

const ComicsList = (props) => {

    const [comicses, setComicses] = useState([]),
          [offset, setOffset] = useState(100),
          [newItemLoading, setNewItemLoading] = useState(false),
          [comicsesEnded, setComicsesEnded] = useState(false);

    const {loading, error, getComicses} = MarvelService();

    useEffect(() => {
        onRequest(offset, true);
    }, [])

    const onRequest = (offset, initial) => {
        initial ? setNewItemLoading(false) : setNewItemLoading(true);
        getComicses(offset)
            .then(onComicsesLoaded)
    }

    const onComicsesLoaded = (newComicses) => {
        let ended = false;
        if (newComicses.length < 8) {
            ended = true;
        }

        setComicses(comicses => [...comicses, ...newComicses]);
        setNewItemLoading(newItemLoading => false);
        setOffset(offset => offset + 8);
        setComicsesEnded(comicsesEnded => ended);
    }

    const renderItems = (arr) => {
        const items = arr.map(item => {
            return (
                <li
                    key={item.id}
                    tabIndex={0}
                    className="comics__item"
                    >
                    <Link to={`/comics/${item.id}`}>
                        <img src={item.thumbnail} alt={item.title} className="comics__item-img"/>
                        <div className="comics__item-name">{item.title}</div>
                        <div className="comics__item-price">{item.price}</div>
                    </Link>
                </li>
            )
        })

        return (
            <ul className='comics__grid'>
                {items}
            </ul>
        )
    }
    const items = renderItems(comicses),
        errorMessage = error ? <ErrorMessage/> : null,
        spinner = loading && !newItemLoading ? <Spinner/> : null;

    return (
        <div className="comics__list">
            {errorMessage}
            {spinner}
            {items}
            <button onClick={() => onRequest(offset)}
                    style={{'display': comicsesEnded ? 'none' : 'block'}}
                    className="button button__main button__long"
                    disabled={newItemLoading}>
                <div className="inner">load more</div>
            </button>
        </div>
    )
}

export default ComicsList;