import {useState, useEffect, useRef} from "react";

import './charList.scss';
import MarvelService from '../../services/MarvelService';
import Spinner from '../spinner/Splinner';
import ErrorMessage from '../errorMessage/ErrorMessage';

const CharList = (props) => {

    const [chars, setChars] = useState([]),
          [newItemLoading, setNewItemLoading] = useState(false),
          [offset, setOffset] = useState(210),
          [charEnded, setCharEnded] = useState(false);

    const {loading, error, getAllCharacters} = MarvelService();

    useEffect(() => {
        onRequest(offset, true);
    }, [])

    const onRequest = (offset, initial) => {
        initial ? setNewItemLoading(false) : setNewItemLoading(true);
        getAllCharacters(offset)
            .then(onCharsLoaded)
    }

    const onCharsLoaded = (newChars) => {
        let ended = false;
        if (newChars.length < 9) {
            ended = true;
        }

        setChars(chars => [...chars, ...newChars]);
        setNewItemLoading(newItemLoading => false);
        setOffset(offset => offset + 9);
        setCharEnded(charEnded => ended);
    }

    const itemRefs = useRef([]);

    const focusOnItem = (id) => {
        itemRefs.current.forEach(item => item.classList.remove('char__item_selected'));
        itemRefs.current[id].classList.add('char__item_selected');
        itemRefs.current[id].focus();
    }

    const renderItems = (arr) => {
        const items = arr.map((item, i) => {
            let notFoundImage = {'objectFir': 'cover'}
            if (item.thumbnail === 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg') {
                notFoundImage = {'objectFit': 'unset'}
            }
            return (
                <li
                    tabIndex={0}
                    ref={el => itemRefs.current[i] = el}
                    className="char__item"
                    key={item.id}
                    onClick={() => {
                        props.onCharSelected(item.id);
                        focusOnItem(i);
                    }}
                    onKeyPress={(e) => {
                        if (e.key === '' || e.key === 'Enter') {
                            props.onCharSelected(item.id);
                            focusOnItem(i);
                        }
                    }
                    }>
                    <img src={item.thumbnail} style={notFoundImage} alt={item.name}/>
                    <div className='char__name'>{item.name}</div>
                </li>
            )
        })

        return (
            <ul className='char__grid'>
                {items}
            </ul>
        )
    }
    const items = renderItems(chars),
          errorMessage = error ? <ErrorMessage/> : null,
          spinner = loading && !newItemLoading ? <Spinner/> : null;

    return (
        <div className="char__list">
            {errorMessage}
            {spinner}
            {items}
            <button onClick={() => onRequest(offset)}
                    style={{'display': charEnded ? 'none' : 'block'}}
                    className="button button__main button__long"
                    disabled={newItemLoading}>
                <div className="inner">load more</div>
            </button>
        </div>
    )
}

export default CharList;